import React from 'react'
import { useState, useRef } from 'react'
import '../../style/modal/portmodal.scss'
import CountryData from '../countryJson/cs.json'
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import axios from 'axios';

export default function DocumentModal() {

    const [show, setShow] = useState(false);
    const [newOption, setNewOption] = useState('');

    const [document, setDocument] = useState('');


    const handleClose = () => {
        setShow(false);
        window.location.reload();
        setNewOption('');
    };

    const handleShow = () => setShow(true);
    const handleAddOption = () => {
        const newOption = document;

        if (newOption.trim() !== '') {
            // onAddOption(newOption); // Call the callback to add the new option to the main page state
            var document_values = { document }
            console.log('doc data:', document_values);
            
            axios.post('http://127.0.0.1:8000/expo/documentadd/', document_values)
                .then(response => {
                    // Handle the response from the server

                    console.log('API response:', response.data);

                    handleClose();
                })
                .catch(error => {
                    // Handle any errors
                    console.log(error)
                    handleClose();
                })
        };
    }


    return (
        <>
            <Button variant="primary" onClick={handleShow}>
                +
            </Button>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title><h2>Add Documents</h2></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <label>
                        Name of Documents:
                        <input type="text" name="doc" className="form-control" value={document} onChange={(e) => setDocument(e.target.value)}/>
                    </label>

                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={handleAddOption}>
                        Add
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    );

}
